.admin-department-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 1px solid #ccc;
  background-color: #fff;
  height: 70px;
}
.admin-department-header .tablehead {
  display: flex;
  align-items: center;
  gap: 10px;
  border: 1px solid #ccc;
  padding: 5px;
  border-radius: 5px;
  font-weight: bold;
}
.admin-department-header .tablehead input {
  border: none;
  outline: none;
}

.admin-student-table thead th {
  font-weight: bold;
}/*# sourceMappingURL=style.css.map */