.take-exam-container{
    background-color: white;
    width: 100%;
    height: calc(100vh - 72px);

    .main-continer{
        padding: 20px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;


    }

    .exam-info-body{
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        gap: 10px;
        background-color: white;
        width: 100%;
        min-height: 600px;

        ul{
            list-style: none;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            background-color: white;
            width: 60%;
            min-height: 500px;
            line-height: 50px;
            padding: 20px;
            border-radius: 10px;
            box-shadow: 0 0 10px rgba(0, 0, 0, 0.2), inset
            0 -155px 278px 0 rgba(8, 50, 235, 0
            ), inset 0 0 0 0 #ecf0f1, inset 0 0 0 0 #ecf0f1;
        }

        h1{
            font-size: 24px;
            color: red;
        }

        li{
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 100%;
            padding: 5px;
            border-radius: 5px;
            background-color: #ecf0f1;
            font-size: 18px;
            font-weight: bold;
            color: #2c3e50;
        }

       
    }



}

.start-exam-btn{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #2ecc71;
    color: white;
    border: none;
    padding: 5px;
    border-radius: 5px;
    font-size: 20px;
    width: 100px;
    font-weight: bold;
    cursor: pointer;
    transition: all 0.3s ease;
    &:hover{
        background-color: #27ae60;
    }
}


.exam-info-fotter{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;


    input[type="checkbox"] {
        
        
    }


}