.attendace {
  width: 100%;
}

.attendace__header {
  height: 80px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
}

.attendace_main_container {
  width: 100%;
}

.attdence-model {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.modal-content-attdence {
  width: 30%;
  max-width: 900px;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
  max-height: 80vh;
}
.modal-content-attdence h2 {
  text-align: center;
  font-size: 20px;
  font-weight: 400;
}
.modal-content-attdence .modal-body-attdence {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  margin-bottom: 20px;
}
.modal-content-attdence .modal-body-attdence p {
  font-size: 20px;
  font-weight: 400;
  padding: 20px;
}
.modal-content-attdence .model-footer-attdence {
  display: flex;
  gap: 20px;
  justify-content: center;
  align-items: center;
}
.modal-content-attdence .model-footer-attdence button {
  height: 50px;
  width: 150px;
  border-radius: 10px;
  color: white;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  border: none;
  outline: none;
}
.modal-content-attdence .model-footer-attdence button.cancle-btn {
  background-color: rgb(255, 0, 0);
}
.modal-content-attdence .model-footer-attdence button.creaet-btn {
  background-color: rgba(0, 0, 0, 0.594);
}

.show-code {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

.attdence-code-content {
  min-width: 40%;
  max-width: 900px;
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  overflow-y: auto;
  max-height: 80vh;
}
.attdence-code-content .code {
  font-weight: bold;
  font-size: 80px;
}

.update-btn button {
  height: 50px;
  width: 150px;
  border-radius: 10px;
  font-size: 20px;
  font-weight: bold;
  cursor: pointer;
  border: solid black 1px;
  outline: none;
  margin-top: 20px;
  margin-bottom: 20px;
  color: black;
  background-color: white;
}
.update-btn button:hover {
  background-color: black;
  color: white;
}/*# sourceMappingURL=attendace.css.map */