.class-details-container{
    height: calc(100vh - 72px);
    overflow: hidden;
}

.class-header {
    display: flex;
    align-items: center;
    padding: 10px;
    border-bottom: solid rgb(204, 199, 199) 2px;
    height: 50px;

    .back {
        background-color: rgb(95, 189, 220);
        margin-top: 15px;
        margin-left: 20px;
        font-size: 30px;
        padding: 5px;
        font-weight: bold;
        border-radius: 50%;


        &:hover{
            background-color: rgb(43, 146, 142);
            transform: scale(1.1);
        }



    }
    h3 {
        margin-left: 20px;
        margin-top: 10px;
        text-transform: capitalize;
        font-size: 25px;
    }
    p {
        font-weight: bold;
        font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
        margin-left: 20px;
        font-size: 20px;
    }
}

    .card-item {
        margin-top: 10px;

        display: flex;
        gap: 10px;
        justify-content: space-around;

        .card {
            margin: 10px;
            height: 50px;
            min-width: 70px;
            max-width: auto;
            background-color: white;
            border-radius: 10px;
            align-items: center;
            justify-content: center;
            display: flex;
            gap: 7px;
            cursor: pointer;
            border: solid gray 1px;
            transition: all 0.5s ease-in-out;
            &:hover {
                transform: scale(1.1);
            }

            .icon {
                display: flex;
                justify-content: center;
                align-items: center;
                height: 30px;
                width: 30px;
                background-color: rgb(215, 207, 207);
                border-radius: 50%; // To make it circular
                font-weight: bold;
                font-size: 30px;
            }

            .card-body {
                button{
                   outline: none;
                   border: none;
                     background-color: transparent;
                    }
                
                h4 {
                    font-size: 20px;
                    font-weight: bold;
                    color: gray;
                    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
                }
                p {
                    font-size: 15px;
                    font-weight: bold;
                    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
                }
            }
        }

        .card-attdance{
            margin: 10px;
            height: 50px;
            // width: 17%;
            background-color: white;
            border-radius: 10px;
            align-items: center;
            justify-content: center;
            display: flex;
            gap: 7px;
            cursor: pointer;
            border: solid gray 1px;
            transition: all 0.5s ease-in-out;
            &:hover {
                transform: scale(1.1);
            }


            .link-to-attdance{
                display: flex;
                justify-content: center;
                align-items: center;
                gap: 10px;
                margin-left: 5px;

                .icon {

                    display: flex;
                    justify-content: center;
                    align-items: center;
                    height: 50px;
                    width: 50px;
                    background-color: rgb(215, 207, 207);
                    border-radius: 50%; // To make it circular
                    font-weight: bold;
                    font-size: 30px;
                }

                .card-body {
                    .link-styles{
                        text-decoration: none;
                        color: black;
                        }
                    
                    h4 {
                        font-size: 20px;
                        font-weight: bold;
                        color: gray;
                        font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
                    }
                    p {
                        font-size: 15px;
                        font-weight: bold;
                        font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
                    }
                }
            }

        }


    }


    .all-btn{
        display: flex;
        justify-content: center;
        align-items: center;
        margin-top: 10px;
        margin-bottom: 20px;
        gap: 20px;
        .assigemnt-btn{
            width: 200px;
            border: solid 1px black;
            padding: 10px;
            border-radius: 10px;
            font-weight: bold;
            font-size: 20px;
            cursor: pointer;
            transition: all 0.5s ease-in-out;
            &:hover{
                transform: scale(1.1);
            }
        }

        .attendance-btn{
            width: 200px;
            border: solid 1px black;
