            }

        }


        .menu-active{
            background-color: rgb(43, 146, 142);
            color: white;
        }
       
    }

.main-section {
    display: flex;
    gap: 10px;
    scroll-behavior: smooth;
    overflow-y: auto;
  
    .main-element {
        width: 100%;
        min-height: 100px;
        max-height: 300px;
        padding-left: 20px;
        padding-right: 20px;
        margin: 10px;
        border-radius: 10px;
        margin-bottom: 100px;

        // desktop only media 
        @media screen and (min-width: 1500px) {
            max-height: 500px;
        }

    }

}

