
.assigemnt-submission{

  .assigment-section{
    height: calc(100vh -  72px);
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;

    .assigment-details-header{
      width: 90%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: solid 2px #d7d4d4;
      margin-bottom: 10px;

      .submission-btns{
        display: flex;
        gap: 20px;

        .menu-btn{
          background-color: rgb(122, 116, 218);
          padding: 8px;
          border-radius: 5px;
          border: none;
          font-weight: bold;
          font-size: 18px;
          cursor: pointer;
          height: 40px;
          min-width: 150px;
          width: auto;
          transition: all 0.5s ease-in-out;

         .active-menu {
            background-color: #007bff;
            color: white;
            font-weight: bold;
          }
          

          &:hover{
            transform: scale(1.1);
            background-color: white;
            border: solid 1px black;
          }
        }
      }


        .assigemnt-back-btn{
          color: red;
          font-weight: 600;
          
          &:hover{
          transform: scale(1.1);
        }
      }
    }

  }

}




.assigment-section {
  height: calc(100vh -  80px);
  padding: 20px;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;

  .assigment-details-forms {
    width: 80%;
    z-index: 10;
    position: fixed;
    overflow-y: auto;
    max-height: 100%;
  }

  .assigment-body {
    width: 80%;
    min-height: calc(100vh -  200px);
    max-height: auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    label {
      width: 100%;
      display: block;
      font-weight: bold;
      margin-bottom: 5px;
    }

    input {
      width: 80%;
      font-weight: 600;
      font-size: 20px;
      padding: 15px;
      margin-bottom: 15px;
      border: 1px solid #ddd;
      border-radius: 4px;
      box-sizing: border-box;
      font-size: 14px;

      &:focus {
        outline: none;
        border-color: #3498db;
        box-shadow: 0 0 5px rgba(52, 152, 219, 0.5);
      }
    }

    .short-form{
      display: flex;
      gap: 10px;
      flex-wrap: wrap;

      .assigemnt-type,
      .assigemnt-deadline{
        width: 40%;
      }

    }

    .editor {
      border: solid gray 1px;
      width: 100%;
      min-height: 100px;
      max-height: 300px;
      overflow-y: auto !important;
      background-color: white;
  }

  .assignment-question{
    margin-top: 20px;
    margin-bottom: 50px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    .item{
      margin-top: 10px;
      padding: 20px;
      margin-bottom: 30px;
      background-color: white;
      border-radius: 5px;
      display: flex;
      justify-content: space-between;
      box-shadow: 0 2px 2px 0 rgba(156, 127, 127, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.19); 
    }

  }
  

    .update-btn{
      margin-bottom: 100px;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;


      button{
        margin-top: 20px;

      width: 200px;
      background-color: #3498db;
      color: #fff;
      padding: 10px;
      border-radius: 5px;
      border: none;
      font-weight: bold;
      font-size: 20px;
      cursor: pointer;
      transition: all 0.5s ease-in-out;
      &:hover{
        transform: scale(1.1);
      }

    }
    }

  }

  // breakpoint 1100
  @media screen and (min-width: 768px) and (max-width: 1100px){
    .assigment-body{
      width: 100%;
      margin-right: 100px;
    }
  }
}



.assigment-submit-table{
  width: 95%;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
  background-color: white;
  border-radius: 5px;
  overflow: hidden;




  // breakpoint 1100
  @media only screen and (max-width: 1100px) {
    width: 100%;
  }
}