.main-exam-continer{
    width: 100%;
    height: calc(100vh - 72px);
    background-color: white;
    overflow: hidden;
}

.exam-details-header{
    width: 100%;
    min-height: 100px;
    height: auto;
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.5);

    .title{
        font-size: 25px;
        font-weight: 600;
        color: #2c3e50;
        border-right: 1 px solid gray;
    }


    .right-side{
      margin-right: 100px;
      display: flex;
      flex-direction: row;
      gap: 10px;
    }





  
}

.main-exam{
    margin-top: 10px;
    display: flex;
    flex-direction: row;
    gap: 10px;
}

.question-answer-section{
    width: 70%;
    height: 650px;
    padding: 0 2%;
    overflow-y: auto;

    .short{
        overflow-y: scroll;
    }
    
}



.timer-chat{
    width: 30%;
    padding: 0 2%;
    height: 700px;
}

// fotter button

.fotter-button{
    width: 100%;
    height: 60px;
    background-color: rgb(233, 231, 231);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    padding: 0 2%;

    button{
        width: 150px;
        height: 40px;
        background-color: rgb(173, 186, 189);
        border-radius: 10px;
        border: none;
        outline: none;
        cursor: pointer;
        font-size: 20px;
        font-weight: 600;
        color: white;
        box-shadow: 0px 0px 2px 0px rgba(0,0,0,0.75);
    }
}


// short question
.short-question {
    padding: 10px;
    background-color: rgb(236, 233, 233);
    border-radius: 5px;
    width: 100%;
    min-height: 150px;
    height: auto;
    margin-bottom: 15px;
  
    .question {
      width: 100%;
      height: auto;
      min-height: 50px;
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: 10px;
  
      p {
        font-size: 20px;
      }
    }
  
    .answer {
      width: 100%;
      height: auto;
      min-height: 50px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
  
      .answer-area {
        min-height: 70px;
        max-height: 70px;
        max-width: 97%;
        min-width: 97%;
        background-color: white;
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 20px;
        resize: none;
        padding: 5px;
  
        
      }
    }
  }
  

//   long question asnwer 

.long-question {
    padding: 10px;
    background-color: rgb(236, 233, 233);
    border-radius: 5px;
    width: 100%;
    height: auto;
    min-height: 100px;
    margin-bottom: 15px;
  
    .longs-question {
      width: 100%;
      height: auto;
      min-height: 50px;
      display: flex;
      flex-direction: row;
      align-items: center;
      gap: 10px;
  
      p {
        font-size: 20px;
      }
    }
  
    .long-answer {
      width: 100%;
      height: auto;
      min-height: 50px;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
      align-items: center;
  
      .long-answer-area {
        margin-bottom: 10px;
        min-height: 300px;
        max-height: 500px;
        max-width: 97%;
        min-width: 97%;
        background-color: white;
        border-radius: 10px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 20px;
        resize: none;
        padding: 5px;
  
        
      }
    }
  }
  



  // timer

  .timer{
    display: flex;
  }