.course-content-container {
  padding: 10px;
}

.student-course-content-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: mediumseagreen;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 10px;
}
.student-course-content-header h4 {
  font-size: 24px;
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
}

.spinner {
  border: 4px solid rgba(0, 0, 0, 0.1);
  border-left-color: #09f;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
.error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 16px;
}

.error-text {
  color: red;
  margin-bottom: 8px;
}

.retry-button {
  background-color: #007bff;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.retry-button:hover {
  background-color: #0056b3;
}

.files-container {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.file-item {
  display: flex;
  align-items: center;
  background-color: rgb(223, 227, 230);
  padding: 10px;
  border-radius: 5px;
}

.download-link {
  margin-right: 8px;
  text-decoration: none;
  color: #007bff;
  font-size: 24px;
}

.download-link:hover {
  color: #0056b3;
}

.file-name {
  font-size: 16px;
}

.download-icon {
  font-size: 24px;
}/*# sourceMappingURL=CourseContent.css.map */