.calender-container-main{
    width: 100%;
    height: calc(100vh - 70px);
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;


    .calendar-container{
      margin-top: 5px;
      position: relative;
      overflow: hidden;
      height: 100%;
      width: 99%;
      background-color: white;
      box-shadow: 0 0 10px 0 rgba(0,0,0,0.2);
    }

}



.close-delete-btn{
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  gap: 20px;
  align-items: center;


}


.event-details {
  padding: 16px;
  background-color: #f7f7f7;
  border-radius: 8px;
}

.event-title {
  font-size: 20px;
  margin-bottom: 10px;
}

.event-datetime {
  font-size: 16px;
  color: #555;
}


.color-selector {
  display: flex;
  margin: 10px 0;
  justify-content: space-around;
}

.color-button {
  width: 50px;
  height: 50px;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  outline: none;
}

.color-button.selected {
  border: 2px solid #333; /* Add a border to indicate the selected color */
}