.admin-student {
  height: calc(100vh - 80px);
  overflow: hidden;
}
.admin-student .admin-student-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #fff;
  padding: 10px 20px;
  border-bottom: 1px solid #ccc;
}
.admin-student .admin-student-header .student-add-btn {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
}

.admin-student-table {
  display: flex;
  flex-direction: column;
  overflow: auto;
  width: 100%;
  height: 85%;
}

.admin-student-table-header {
  position: sticky;
  top: 0;
  background-color: white;
  z-index: 1;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
}

.pagination {
  position: fixed;
  bottom: 0;
  width: 95%;
  margin-left: -7%;
  background-color: white;
  border-top: 1px solid #ccc;
  box-shadow: 0 -2px 2px rgba(0, 0, 0, 0.2);
  border-right: none;
}

.admin-student-table-body {
  border-bottom: none;
  overflow: auto;
}/*# sourceMappingURL=student.css.map */