.attendacne-code {
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}
.attendance-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    .attendance-instructions {
        display: flex;
        flex-direction: row;
    }
    input {
        width: 30%;
        padding: 10px;
        border: 1px solid #000;
        border-radius: 5px;
        outline: none;
        font-size: 1.5rem;
        font-family: 'Roboto', sans-serif;
        font-weight: bold;
        color: #000;
        transition: all 0.3s ease;
        &:focus {
            border: 1px solid red;
        }
    }
    button {
        margin-top: 20px;
        width: auto;
        padding: 15px;
        border: 1px solid #000;
        background-color: white;
        border-radius: 5px;
        outline: none;
        font-size: 1rem;
        font-family: 'Roboto', sans-serif;
        font-weight: 500;
        color: #000;
        transition: all 0.3s ease;
        cursor: pointer;
        &:hover {
            background-color: #000;
            color: #fff;
        }
    }
}



// attendance table style

.attendance-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;

    .student-attendance-header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        background-color: darkslateblue;
        color: white;
        padding: 10px;
        border-radius: 5px;
    }
  
    .student-attendance-table {
      width: 100%;
      border-collapse: collapse;
      margin: 20px 0;
      font-size: 16px;
      text-align: left;
  
      th, td {
        padding: 12px;
        border: 1px solid #ddd;
      }
  
      th {
        background-color: #f4f4f4;
      }
  
      tr:hover {
        background-color: #f9f9f9;
      }
  
      .absent-cell {
        color: red;
      }
    }
  }
  