
.settings{
    width: 100%;
    height: calc(100vh - 72px);
    background-color: white;


    .setting-menu-header{
        padding: 20px;
    }
}

.setting-menu{
    background-color: rgb(245, 248, 247);
    width: 100%;






    .setting-menu-items{
            display: flex;
            margin-left: 50px;
            margin-right: 50px;
            gap: 10px;


            button{
                padding: 10px;
                width: auto;
                border: none;
                background-color: transparent;
                cursor: pointer;
                font-size: 18px;
                font-weight: bold;
                color: black;
                outline: none;
                display: flex;
                justify-content: center;
                align-items: center;

                &:hover{
                    color: rgb(152, 154, 251);
                }
            }

        
            .act-menu{
                background-color: white;
                color: black;
                border-radius: 5px;
                border: solid 1px rgb(152, 154, 251);
            }

        }

}




